// 岗位等级管理
import http from '../httpConfig'

const table = "mrbase_sys_job_post_grades"


// 获取岗位等级列表接口
export const get_postgrades = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/post/grade/list-post-grades/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

