<template>
  <div>
    <mrc_ess_table v-if="$route.name=='Post_level_management'"
                   :addBtnList='listmenu'
                   :headers='headers'
                   :paginationOptions='options'
                   @turnPage='getlist'
                    :hideAddBtn="!hasBtn('add-job-position-level')"
                   :isLoading='loading'
                   :slotKey="['hierarchyName','org_level_name']"
                   :items='desserts'>
      <template v-slot:top>
        搜索框
      </template>

      <!-- 层级名称 -->
      <template v-slot:item.hierarchyName="{item}">
        <span class="f1">{{item.group_name}}</span><br>
        <span class="f2">{{item.job_grade_name}}</span>
      </template>

      <!-- 使用单位 -->
      <template v-slot:item.org_level_name="{item}">
        <!-- <div v-html="item.org_level_name"
                   class="spanbox"></div> -->
        <span class="chipbox">
          <v-chip class="ml-2"
                  v-for="(item1,index1) in item.org_level_name"
                  :key="index1">
            {{item1}}
          </v-chip>
        </span>
      </template>
      <!-- 按钮 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <template v-else>
      <router-view></router-view>
    </template>
  </div>
</template> 

<script>
import { get_postgrades, delete_postgrades, delete_postgrades_group } from '@/service/api/postgrades.js'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      desserts: [],
    };
  },
  created () { },
  async mounted () {
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    turnPage ({ cpage, pagesize }) {
      this.options.cpage = cpage
      this.options.pagesize = pagesize
      this.getlist()
    },
    async getlist (obj) {  //获取岗位列表  
      this.loading = true
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const org_level_uuid = this.$store.state.currentCompany
      const res = await get_postgrades({
        cpage: this.options.cpage,
        pagesize: this.options.pagesize,
        org_level_uuid
      })
      if (res.code == 200) {
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
        this.desserts = res.data.map(item => {
          // item.org_level_name = '后端,还未返回,这个,字段'   //后端未改接口 前端自己添加
          item.org_level_name = (item.org_level_name.indexOf(',') != -1) ? item.org_level_name.split(',') : [item.org_level_name];
          return item
        })
      } else {
        this.$store.commit('newsTips', res.msg)
      }
      this.loading = false
    },
    godetail (item) {  //行内进编辑详情页
      console.log(111);
      const groupName = item.group_name
      this.$store.commit('set_uuid', item.uuid)
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_subPage_name', groupName ? groupName : item.job_grade_name)
      this.$router.push({
        name: 'detailPost',
        params: {
          uuid: item.uuid,
          iconstatus: true,
          groupName
        }
      })
    },
    // 编辑
    editPrompt (item) { //详情页 
      const groupName = item.group_name
      this.$store.commit('set_uuid', item.uuid)
      this.$store.commit('set_iconstatus', false)  //行内进图标显示
      this.$store.commit('set_subPage_name', groupName ? groupName : item.job_grade_name)
      this.$router.push({
        name: 'detailPost',
        params: {
          uuid: item.uuid,
          iconstatus: false,
          groupName
        }
      })
    },
    // 删除 
    handleDel (item) {
      this.$myConfirm({
        content: '确认删除该岗位等级？'
      }).then(() => {
        if (item.group_name) {
          delete_postgrades_group({
            group_name: item.group_name
          }).then(res => {
            if (res.code == 200) {
              this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
              if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
              this.getlist()
            }
          })
        }
        else {
          delete_postgrades({
            uuid: item.uuid
          }).then(res => {
            if (res.code == 200) {
              this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
              if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
              this.getlist()
            }
          })
        }
      }).catch(() => { })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.editPrompt(item)
          break
        case 2:
          this.handleDel(item)
          break
      }
    }
  },
  computed: {
    listmenu () {
      return [
        {
          page_name: 'addOne',
          name: this.$t('label_Add_a_single_level'),
          show: true
        },
        {
          page_name: 'addGroup',
          name: this.$t('label_A_set_of_new'),
          show: true
        }
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View'), show: this.hasBtn('view-job-position-level') },
        { name: this.$t('label_Edit'), show: this.hasBtn('edit-job-position-level') },
        { name: this.$t('label_delete'), show: this.hasBtn('delete-job-position-level') },
      ]
    },
    headers () {
      return [
        {
          text: this.$t('label_The_name_of_the_hierarchy'),
          align: 'start',
          sortable: false,
          value: 'hierarchyName',
        },
        {
          text: this.$t('label_The_hierarchy'),
          align: 'start',
          sortable: false,
          value: 'job_grade_class',
        },
        {
          text: this.$t('label_Use_the_unit'),
          align: 'start',
          sortable: false,
          value: 'org_level_name',
        },
        {
          text: this.$t('label_Number_of_posts_in_use'),
          align: 'start',
          sortable: false,
          value: 'staff_number',
        },
        {
          text: '',
          value: 'actions',
          sortable: false,
          align: 'start',
        },
      ]
    }
  },
  watch: {
    $route (v) {
      if (this.$route.name == 'Post_level_management') {
        // console.log('更新list');
        this.getlist()
      }
    }
  },
};
</script>

